@import "../variables/variables";
@import "../mixins/fluid-mixin";

body {
    font-family: $ff-default;
    color: $colorWhite;
    background-color: $colorWhite;
    font-weight: 400;
    line-height: normal;
    min-width: 320px;
    font-size: 2rem;
    @media (min-width: 768px) {
        font-size: 2.8rem; }
    &._loaded {
        .header,
        .footer {
            opacity: 1;
            transform: translateY(0); }
        .decor-page {
            opacity: 1; } } }
